.newsletter{
    width: 80%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 5px 14px;
    background: #ebecf0;
    gap: 30px;
    margin-top: 700px;
}

.newsletter p{
    color: #454545;
    font-size: 20px;
}
.newsletter  h1{
    color: #454545;
    font-size: 55px;
    margin-top: 20px;
    font-weight: 600;
}
.newsletter div {
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid #e3e3e3;
} 
.newsletter input {
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: #616161;
    font-family: Poppins;
    font-size: 16px;
}
.newsletter div button {
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}