.popular {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
}

.popular h1 {
    color: #171711;
    font-size: 50px;
    font-weight: 600;
    margin-top: 10vh;
   
}

.popular hr {
    width: 200px;
    height: 7px;
    border: solid #FF4141;
    border-radius: 10px;
    
}

.popular-item {
    margin-top: 50px;
    display: flex;
    gap: 30px;
}