.new-collections {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
    margin-top: 80px;
}

.new-collections h1 {
    color: #171711;
    font-size: 50px;
    font-weight: 600;
    margin-top: 10vh;
   
}

.new-collections hr {
    width: 200px;
    height: 7px;
    border: solid #FF4141;
    border-radius: 10px;
    
}

.collections{
   display: grid;
   grid-template-columns: 1fr 1fr 1fr 1fr;
   margin-top: 50px;
   gap: 30px;
}