.cartitem{
    margin: 100px 170px;
}
.cartitem hr{
    height: 3px;
    background: #e2e2e2;
    border: 0;
}
.cartitem-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items:center ;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitem_format{
    font-size: 17px;
    font-weight: 500;
}
.cartion_product-icon{
   height: 62px; 
}
.cart-remove-item{
    width: 15px;
    margin:0px 40px;
    cursor: pointer;
}
.cartitem-quantity{
   width: 64px;
   height: 50px; 
   border: 2px solid #ebebeb;
   background: #fff;
}
.cartitems-down{
    display: flex;
    margin: 100px 0px;

}
.cartitem-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}
.cartitem-total-item{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitem-total button{
    width: 262px;
    height: 58px;
    outline: none;
    border: none;
    background: #ff5a5a;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;

}
.cartitem-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;


}
.cartitem-promocode p{
    color: #555;
}
.cartitems-promobox{
    width:504px ;
    padding-left: 20px;
    margin-top: 15px;
    height: 58px;
    background:#eaeaea ;
}
.cartitems-promobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}
.cartitems-promobox button{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: black;
    color:white ;
    cursor: pointer;
    margin-top: 30px;
    margin-left: 90 px;
    
}
