.navbar {
    position: fixed;  /* Fix the navbar at the top */
    top: 0;           /* Position it at the top of the viewport */
    left: 0;          /* Position it at the left of the viewport */
    width: 100%;      /* Take up the full width of the viewport */
    display: flex;
    justify-content: space-between;
    padding: 10px;
    box-shadow: 0 1px 3px -2px black;
    height: 70px;
    z-index: 1000;    /* Ensure the navbar is above other elements */
    background: #f3f3f3;
}

.nav-logo {
    display: flex;
    align-items: center;
    gap: 9px;
   
}

.nav-logo p {
    color: #171717;
    font-size: 38px;
    font-weight: 600;
}

.nav-logo img {
   width: 140px;
   margin-top: -29px;
}

.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3;
    cursor: pointer;
   
}

.nav-menu hr {
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
}

.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 45px;
}

.nav-login-cart button {
    width: 120px;
    height: 40px;
    outline: none;
    border: 1px solid #ff4141;
    border-radius: 75px;
    color: #7c7b7b;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
    margin-bottom: 29px;
}

.nav-login-cart img{
   width: 30px;
   margin-bottom: 29px;
}
.nav-login-cart button:active {
    background: #f3f3f3;
}

.nav-cart-count {
    width: 19px;
    height: 19px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -45px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: #ff4141;
    color: white;
}