.offre {
    text-align: center;
    margin-top: 50px;
    /* Adjust the margin-top as needed */

    h1 {
        color: #171711;
        font-size: 50px;
        font-weight: 600;
        margin-top: 13vh;
    }

    hr {
        width: 200px;
        height: 2px;
        border: solid #FF4141;
        border-radius: 10px;
        margin: 20px auto;
        background-color:  #FF4141;
        /* Adjust the margin as needed */
    }

    .offers {
        img {
            max-width: 100%;
            height: auto;
            border-radius: 8px;
            /* Optional: Add border-radius for a rounded image */
            margin-top: 20px;
            /* Adjust the margin-top as needed */
        }
    }
}