.hero {
  height: 90vh;
  width: 100%;
  background-size: cover;
  background-position: top 25% right -300px;
  padding: 0 80px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  transition: background-image 0.5s ease;
}

.hero-left {
    flex: 1;
    flex-direction: column;
    justify-content: center;
    /* gap: 20px; */
    padding-left: 50px;
    line-height: 1.1;
    margin-top:100px ;
    margin-left: -50px;
}

.hero-left h2 {
    color: #090009;
    font-size: 26px;
    font-weight: 600;
}

.hero-left p {
    color: #171717;
    font-size: 100px;
    font-weight: 650;
}

.hero-lastest-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 20px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
}

.hero-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
